import React from "react";

import "./singleExam.css";

import moment from "moment";

import testIcon from './assets/test.svg';

import DateCountdown from "react-date-countdown-timer";

import {Modal, Button} from 'react-bootstrap';

class SingleExam extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
            showBtn: true,
            showExpired: false,
            showFrom : false,
            showTo : false,
            openModal : false,
		};
	}

	componentDidMount = () => {
		this.props.exam.attend.length > 0 &&
			this.props.exam.attend.map((item) => {
				if (item === this.props.studentId) {
					this.setState({
						showBtn: false,
					});
				}
            });
            
        if (new Date(this.props.exam.startDate) > new Date()) {
            this.setState({
                showFrom : true
            })
        }

        if (new Date(this.props.exam.endDate) > new Date() && (new Date(this.props.exam.startDate) === new Date() || new Date(this.props.exam.startDate) < new Date())) {
            this.setState({
                showTo: true,
            });
        }

        if (new Date(this.props.exam.endDate) < new Date()) {
            this.setState({
                showExpired: true,
            });
        }

		this.setState({
			loading: false,
		});
    };
    
    handleFromComplete = () => {
		this.setState({
			showFrom: false,
			showTo: true,
		});
	};

	handleToComplete = () => {
		this.setState({
			showTo: false,
			showExpired: true,
		});
    };
    
    handleStartExam = () => {
        this.setState({
            openModal : false
        },() => {
            this.props.sendExamData(this.props.exam, true);
        })
    }

	render() {
		return (
			<div className='single-exam-container'>
				{
                    this.state.loading 
                    ? 
                    null 
                    : 
					<>
                        <img src={testIcon} alt='icon' />
                        <div className='exam-head'>
                            <p>{this.props.exam.title}</p>
                            <h5>Total Duration : {this.props.exam.duration} mins</h5>
                            <h5>Subject : {this.props.exam.subjectName}</h5>
                            <span>
                                Start Date : <i>{moment(this.props.exam.startDate).format("LL")}</i>
                            </span>
                            <span>
                                End Date : <i>{moment(this.props.exam.endDate).format("LL")}</i>
                            </span>

                            {
                                this.state.showBtn
                                ?
                                <>
                                {
                                    this.state.showFrom
                                    ?
                                    <button> 
                                        <span className = "timer-text">Active in</span>
                                        <DateCountdown dateTo={this.props.exam.startDate} callback={this.handleFromComplete} />
                                    </button>
                                    :
                                    <>
                                    {
                                        this.state.showTo
                                        ?
                                        <button onClick = {() => this.setState({openModal: true})}>
                                            <span className = "timer-text">Available for</span>
                                            <DateCountdown dateTo={this.props.exam.endDate} callback={this.handleToComplete} />
                                        </button>
                                        :
                                        <button><span>Expired</span></button>
                                    }
                                    </>
                                }
                                </>
                                :
                                <button><span style={{ color: "green" }}>You have taken the exam !</span></button>
                            }
                        </div>
					</>
				}

                <Modal
                    show={this.state.openModal}
                    onHide={() => this.setState({openModal : false})}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Read Carefully !</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        Once you proceed, you cannot go back. Make sure you attempt the whole examination, failing to which will result in low score in your grade book.
                        If you close the exam tab without completing the whole exam , you would still be marked as attempted and you cannot take a re-test.
                        Please follow the strict timer for the exam, once the timer is completed your exam session will get over automatically. Your final grades would be recorded based on your performance. There is no negative marking.
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => this.setState({openModal : false})}>
                            Close
                        </Button>
                        <Button variant="primary" onClick = {() => this.handleStartExam()}>Understood</Button>
                    </Modal.Footer>
                </Modal>
			</div>
		);
	}
}

export default SingleExam;
