import React from "react";

import "./labworkGrade.css";

import { Form } from "react-bootstrap";

import Lottie from "lottie-react-web";
import empty from '../../../../assets/EmptyFolder.json';

class MyLabworkGrade extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			subject: "",
			labworkArray : [],
			filteredArray: [],
		};
	}

	componentDidMount = () => {
		
		var newArray = [];
		this.props.student && this.props.student.assignments.length > 0 && this.props.student.assignments.map((item) => {
			item.attend.map((item2) => {
				var newData = {};
				if (item2.id === this.props.student._id) {
					newData.marks = item2.marks;
					newData.remark = item2.remark;
					newData.subject = item.subjectName;
					newData.title = item.title;
					newArray.push(newData);
				}
			})
		})

		this.setState({
			labworkArray : newArray,
			filteredArray : newArray,
			loading : false
		},() => {
			console.log(this.state.filteredArray);
		})
	};

	handleSetSubject = (e) => {
		this.setState({
			subject: e.target.value,
		},() => {
			this.handleFilter();
		});
	};

	handleFilter = () => {
		var newArray = [];
		this.state.labworkArray.map((item) => {
			if (item.subject === this.state.subject) {
				newArray.push(item);
			}
		})

		this.setState({
			filteredArray : newArray
		})
	};

	render() {
		return (
			<>
			{
				this.state.loading
				?
				null
				:
				<div className='my-assignment-grade-container'>
					<div className='select-sub'>
						<Form.Group className='container-fluid'>
							<Form.Label>Select Subject</Form.Label>
							<Form.Control as='select' name='subject' onChange={this.handleSetSubject}>
								<option selected hidden>
									Choose...
								</option>
								<option value='Mathematics'>Mathematics</option>
								<option value='Science'>Science</option>
								<option value='English'>English</option>
								<option value='Islamic Studies'>Islamic Studies</option>
								<option value='Social Studies'>Social Studies</option>
								<option value='Information Technology'>Information Technology</option>
								<option value='Life Skills'>Life Skills</option>
								<option value='Music'>Music</option>
								<option value='Sports'>Sports</option>
								<option value='Arts'>Arts</option>
							</Form.Control>
						</Form.Group>
					</div>

					<div className = "score-body">
						<div className = "score-table-head">
							<div className = "table-head-left">
								<p>LABWORK TITLE</p>
							</div>
							<div className = "table-head-right">
								<div className = "marks">
									<p>MARKS</p>
								</div>
								<div className = "remarks">
									<p>REMARKS</p>
								</div>
							</div>
						</div>

						<div className = "score-table-body">
						{
							this.state.filteredArray && this.state.filteredArray.length > 0 
							?
							<>
							{
								this.state.filteredArray.map((item) => {
									return (
										<div className = "stat-panel">
											<div className = "panel-left">
												<p>{item.title}</p>
											</div>
											<div className = "panel-right">
												<div className = "marks">
													<p>{item.marks}</p>
												</div>
												<div className = "remarks">
													<p>{item.remark}</p>
												</div>
											</div>
										</div>
									)
								})
							}
							</>
							:
							<>
								<div style={{display : 'flex', flexDirection : 'column', alignItems : 'center'}}>
									<Lottie options={{ animationData: empty }} height = {300} width = {300}/>
									<p>No assignment grades found</p>
								</div>
							</>
						}
						</div>
					</div>
				</div>
			}
			</>
		);
	}
}

export default MyLabworkGrade;
