import React from "react";

import { Modal, Button, Form, Spinner, Col } from "react-bootstrap";
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaPlus, FaCross } from "react-icons/fa";
import { IoMdClose } from "react-icons/io";
import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import ReactTable from "react-table-6";
import { addBehaviour, addBehaviourNote } from "../../../redux/actions/teacherDashboard/teacherActions";
import { connect } from "react-redux";
import axios from "axios";
import moment from "moment";
import { FaArrowLeft } from "react-icons/fa";

class StudentTable extends React.Component {
	constructor() {
		super();
		this.state = {
			showNote: false,
			behaviour: "",
			note: "",
			student: {},
			savingNote: false,
			behaviours: [],
			date: new Date(),
			student: {},
			exams: [
				{
					title: "First Unit Examination",
					date: "27th Jan, 2020",
					attain: "27 th Jan, 2020",
					time: "1hr 2mins",
					marks: 50,
				},
				{
					title: "Second Unit Examination",
					date: "17th Jan, 2021",
					attain: "17 th Jan, 2021",
					time: "1hr 20mins",
					marks: 40,
				},
				{
					title: "Third Unit Examination",
					date: "27th Jan, 2021",
					attain: "27 th Jan, 2021",
					time: "1hr 7mins",
					marks: 49,
				},
			],
			assigments: [
				{
					title: "Assigment 1",
					date: "27th Jan, 2020",
					attain: "27 th Jan, 2020",
					time: "1hr 2mins",
					marks: 50,
				},
				{
					title: "Assigment 2",
					date: "17th Jan, 2021",
					attain: "17 th Jan, 2021",
					time: "1hr 20mins",
					marks: 40,
				},
				{
					title: "Assigment 3",
					date: "27th Jan, 2021",
					attain: "27 th Jan, 2021",
					time: "1hr 7mins",
					marks: 49,
				},
			],
			homeworks: [
				{
					title: "Homework 1",
					date: "27th Jan, 2020",
					attain: "27 th Jan, 2020",
					time: "1hr 2mins",
					marks: 50,
				},
				{
					title: "Homework 2",
					date: "17th Jan, 2021",
					attain: "17 th Jan, 2021",
					time: "1hr 20mins",
					marks: 40,
				},
				{
					title: "Homework 3",
					date: "27th Jan, 2021",
					attain: "27 th Jan, 2021",
					time: "1hr 7mins",
					marks: 49,
				},
			],
			labworks: [
				{
					title: "Performing load balancing and removing parallax 1",
					date: "27th Jan, 2020",
					remark: "nice",
					time: "1hr",
					marks: 50,
				},
				{
					title: "Performing load balancing and removing parallax 2",
					date: "17th Jan, 2021",
					remark: "nice",
					time: "1hr",
					marks: 40,
				},
				{
					title: "Performing load balancing and removing parallax 3",
					date: "27th Jan, 2021",
					remark: "nice",
					time: "1hr",
					marks: 49,
				},
			],
			tab: 1,
		};
	}

	handleTab = (tab) => {
		this.setState({
			tab,
		});
	};

	handleShowInfo = (e) => {
		this.setState({
			student: e,
		});
	};

	render() {
		console.log(this.props.teacher);
		const school = this.props.teacher.schoolInfo;
		var students = [];
		this.props.teacher.students.map((stud) => {
			if (stud.class === this.props.sub.class) {
				students.push(stud);
			}
		});
		const data = students;
		return (
			<div className='grade-book-container'>
				<div className='students-header'>
					<div className='students-stats'>
						<h1>
							<FaArrowLeft onClick={this.props.handleBack} style={{ cursor: "pointer", fontSize: "20px", transform: "translateY(-1px)" }} /> Student List
						</h1>
						<div className='officer-stats'>
							<p>
								TOTAL STUDENTS: <span>{students.length}</span>
							</p>
						</div>
					</div>
				</div>
				{data.length > 0 ? (
					<div className='grade-book-body'>
						<div className='grade-student-list'>
							{data.map((d) => {
								return (
									<div className='student-grade-box'>
										<div className='student-grade-box-body'>
											<img src={"/api/file/show/" + d.photo.filename} alt='profile-pic' />
											<h1>{d.name}</h1>
											<p onClick={this.handleShowInfo}>View Grade book</p>
										</div>
									</div>
								);
							})}
						</div>
						<div className='grade-container'>
							<div className='grade-type-list'>
								<div className={this.state.tab === 1 ? "grade-type active" : "grade-type"} onClick={() => this.handleTab(1)}>
									<p>Exams</p>
								</div>
								<div className={this.state.tab === 2 ? "grade-type active" : "grade-type"} onClick={() => this.handleTab(2)}>
									<p>Assigments</p>
								</div>
								<div className={this.state.tab === 3 ? "grade-type active" : "grade-type"} onClick={() => this.handleTab(3)}>
									<p>Homework</p>
								</div>
								{this.props.sub.lab ? (
									<div className={this.state.tab === 4 ? "grade-type active" : "grade-type"} onClick={() => this.handleTab(4)}>
										<p>Lab Work</p>
									</div>
								) : null}
							</div>
							<div className='grade-book-list'>
								{this.state.tab === 1 && this.state.exams.length > 0 ? (
									<>
										{this.state.exams.map((exam) => {
											return (
												<div className='grade-book-box'>
													<h1>{exam.title}</h1>

													<p>Date : {exam.date}</p>
													<p>Attained Date : {exam.attain}</p>
													<p>Time taken : {exam.time}</p>
													<p>Marks Obtained: {exam.marks}</p>
												</div>
											);
										})}
									</>
								) : null}
								{this.state.tab === 2 && this.state.assigments.length > 0 ? (
									<>
										{this.state.assigments.map((assigment) => {
											return (
												<div className='grade-book-box'>
													<h1>{assigment.title}</h1>

													<p>Date : {assigment.date}</p>
													<p>Attained Date : {assigment.attain}</p>
													<p>Time taken : {assigment.time}</p>
													<p>Marks Obtained: {assigment.marks}</p>
												</div>
											);
										})}
									</>
								) : null}
								{this.state.tab === 3 && this.state.homeworks.length > 0 ? (
									<>
										{this.state.homeworks.map((homework) => {
											return (
												<div className='grade-book-box'>
													<h1>{homework.title}</h1>

													<p>Date : {homework.date}</p>
													<p>Attained Date : {homework.attain}</p>
													<p>Time taken : {homework.time}</p>
													<p>Marks Obtained: {homework.marks}</p>
												</div>
											);
										})}
									</>
								) : null}
								{this.state.tab === 4 && this.state.labworks.length > 0 ? (
									<>
										{this.state.labworks.map((labwork) => {
											return (
												<div className='grade-book-box'>
													<h1>{labwork.title}</h1>

													<p>Date : {labwork.date}</p>
													<p>Remark : {labwork.remark}</p>
													<p>Time taken : {labwork.time}</p>
													<p>Marks Obtained: {labwork.marks}</p>
												</div>
											);
										})}
									</>
								) : null}
							</div>
						</div>
					</div>
				) : null}
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		addBehaviour: (data) => dispatch(addBehaviour(data)),
		addBehaviourNote: (data) => dispatch(addBehaviourNote(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(StudentTable);
