import React from "react";

import "./studentDashboard.css";
import { Link } from "react-router-dom";

import bannerImg from "./assets/27637-welcome.json";
import Lottie from "lottie-react-web";

import { fetch_student_profile_data } from "../../redux/actions/studentDashboard/studentActions";

import { connect } from "react-redux";
import FloralLoading from "./loadingScreen/floralLoading";
import StudentNavbar from "./websiteComponents/Navbar/navbar.component";
import TopNavBar from "./websiteComponents/TopNav/topNav.component";
import StudentFooter from './websiteComponents/footer/footer';

import image3 from './assets/image3.png';
import image4 from './assets/image4.png';
import image5 from './assets/image5.png';
import image6 from './assets/image6.png';
import image7 from './assets/image7.png';
import image8 from './assets/image8.png';
import image11 from './assets/image11.png';
import image14 from './assets/image14.png';
import image13 from './assets/image13.png';


class StudentDashboard extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
		};
	}

	componentDidMount = async () => {
		if (this.props.auth.currentUser._id) {
			const studentData = {
				studentId: this.props.auth.currentUser.ref_id,
			};
			const result = await this.props.fetch_student_profile_data(studentData);

			if (result) {
				this.setState({
					loading: false,
				});
			}
		}
	};

	render() {
		console.log(this.props.student);
		return (
			<>
				{this.state.loading ? (
					<FloralLoading />
				) : (
						<div className="student-homepage-container">
							<TopNavBar />
							<StudentNavbar student={this.props.student} />
							<div className="student-home-banner">
								<div className="banner-content">
									
									<h1 className="animate__animated animate__bounceInLeft">
										We Envision Worlds Best
                					</h1>
									<h1 className="animate__animated animate__bounceInLeft">
									Learning Experience.
                					</h1>
									<p className="animate__animated animate__fadeIn animate__delay-1s">
										IIGPs courses is for every one from different age groups and with various level of skills.
                					</p>
									<button
										type="button"
										className="animate__animated animate__bounceInLeft animate__delay-1s"
									>
										Start Learning
                					</button>
								</div>
								<div className="banner-image">
									<Lottie
										options={{ animationData: bannerImg }}
										width={480}
										height={480}
										style={{zIndex:"10"}}
									/>
									<img src={image3} className = "img-bg"/>
									<img src={image7} className = "img-lines"/>
									<img src={image14} className = "img-bulb"/>
									<img src={image13} className = "img-people"/>
									<img src={image4} className = "img-book-1"/>
									<img src={image5} className = "img-book-2"/>
									<img src={image6} className = "img-compass"/>
									<img src={image8} className = "img-cloud-1"/>
									<img src={image11} className = "img-cloud-2"/>
								</div>
							</div>
							<div className="personal-learning-container">
								<div className="personal-learning-header">
									<div className="personal-learning-text">
										<p>Personal Learning</p>
										<div className="text-dash"></div>
									</div>
								</div>
								<div className="personal-learning-body">
									{this.props.student.classInfo.subjects.map((subject) => {
										if (subject.chapters.length > 0) {
											return (
												<div className="subject-cards">
													<div className="card-head">
														<p>{subject.subjectName}</p>
													</div>
													<div className="card-body">
														{subject.chapters.length > 0 ? (
															<>
																{subject.chapters.map((chapter) => (
																	<div className="subject-item">
																		<div className="chap-name">
																			<p>{chapter.chapterName}</p>
																		</div>
																		<div className="skill">
																			<Link
																				to={
																					"/dashboard/learning/" +
																					subject.subjectName
																				}
																			>
																				{chapter.topics.length} skills
                                    										</Link>
																		</div>
																	</div>
																))}
															</>
														) : (
																<p>EMPTY</p>
															)}
													</div>
													<div className="card-foot">
														<Link
															to={"/dashboard/learning/" + subject.subjectName}
														>
															View More
                          								</Link>
													</div>
												</div>
											);
										}
									})}
								</div>
							</div>
							<StudentFooter />
						</div>
					)}
			</>
		);
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth,
		student: state.student.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		fetch_student_profile_data: (studentData) =>
			dispatch(fetch_student_profile_data(studentData)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(StudentDashboard);
