import React from "react";

import "./attemptExam.css";

import moment from "moment";

import DateCountdown from "react-date-countdown-timer";

import { RiArrowDownSLine } from "react-icons/ri";

import MathJax from "react-mathjax-preview";

import check from "./assets/check.svg";

import { Modal, Button } from "react-bootstrap";

import Lottie from "lottie-react-web";

import thankyou from "./assets/thankyou.json";

import axios from 'axios';

import {connect} from 'react-redux';
import {saveExam} from '../../../../../../redux/actions/studentDashboard/studentActions';

var timer;

class AttemptExam extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			questions: [],
			tab: 0,
			currentQuestion: null,
			currentSecIndex: null,
			currentIndex: null,
			currentSection: null,
			totalMarks: 0,
			actualMarks: 0,
			showModal: false,
			isEnd: false,
			timer : 0,
			totalCorrect : 0,
			totalWrong : 0
		};
	}

	componentDidMount = async () => {

		const data = {
			studentId : this.props.studentId,
			examId : this.props.exam._id
		}

		// Storing studentid in exam attend array so as to avoid re-examination

		var isRegistered = await axios.post("/api/exam/storeAttempt", data);
		if (isRegistered.data !== null && isRegistered.data.type === 'success') {
			var check = await this.handleStoreSeperateSection();
			if (check === "Storing done !") {
				this.handleInitialise();
				this.setState({
					questions: this.props.exam.questions,
					loading: false,
				},() => {
					this.handleStartTimer();
				});
			}
		}
		
	};

	handleStartTimer = () => {
		timer = setInterval(() => {
            this.setState({
                timer : this.state.timer + 1
            })
        }, 1000);
	}

	handleStoreSeperateSection = () => {
		this.props.exam.questions.length > 0 &&
			this.props.exam.questions.map((item, index) => {
				this.setState({
					["section" + index]: item.questions,
					["attemptedSection" + index]: [],
				});
			});
		return "Storing done !";
	};

	handleInitialise = () => {
		var total = 0;
		for (var i = 0; i < this.props.exam.questions.length; i++) {
			this.state["section" + i].map((question, index) => {
				question.index = index;
				total = total + parseInt(question.marks); //Storing total marks of the exam to calculate percentage
			});
		}

		// Setting first question

		var currentQuestion = this.props.exam.questions[0].questions[0];
		currentQuestion.options.map((option, index) => {
			option.selected = false;
			option.index = index;
		});

		this.setState({
			currentSection: this.props.exam.questions[0].name,
			currentQuestion: this.props.exam.questions[0].questions[0],
			currentIndex: 0,
			currentSecIndex: 0,
			actualMarks: total,
		});
	};

	handleSetCurrentQuestion = (ques, key, index) => {
		var currentQuestion = ques;
		currentQuestion.options.map((option, index) => {
			// option.selected = false;
			option.index = index;
		});

		this.setState({
			currentQuestion: currentQuestion,
			currentIndex: key,
			currentSecIndex: index,
		});
	};

	handleNext = () => {
		this.setState(
			{
				currentIndex: this.state.currentIndex + 1,
			},
			() => {
				if (this.state.currentIndex < this.state["section" + this.state.currentSecIndex].length) {
					var found = false;
					this.state["attemptedSection" + this.state.currentSecIndex].map((question) => {
						// check if question has been already attempted
						if (question.index === this.state.currentIndex) {
							// found = true;
							return;
						}
					});

					if (found) {
						// if attempetd then move to next question index
						this.handleNext();
					} else {
						// setting new question
						var currentQuestion = this.state["section" + this.state.currentSecIndex][this.state.currentIndex];
						currentQuestion.options.map((option, index) => {
							option.index = index;
						});

						this.setState({
							currentQuestion: currentQuestion,
						});
					}
				} else {
					this.handleUpgradeSection();
				}
			}
		);
	};

	handleUpgradeSection = () => {
		// increment section index by 1 and set current question index to 0
		this.setState(
			{
				currentSecIndex: this.state.currentSecIndex + 1,
				currentIndex: 0,
			},
			() => {
				if (this.state.currentSecIndex < this.props.exam.questions.length) {
					// set currentQuestion for first question from new section
					var currentQuestion = this.state["section" + this.state.currentSecIndex][this.state.currentIndex];
					currentQuestion.options.map((option, index) => {
						option.selected = false;
						option.index = index;
					});
					this.setState({
						currentQuestion: currentQuestion,
					});
				} else {
					alert("No more Sections");
				}
			}
		);
	};

	handleGetAnswer = (option) => {
		var currentQuestion = this.state.currentQuestion;
		if (currentQuestion.choice === "single") {
			currentQuestion.options.map((op) => {
				if (op.index === option.index) {
					op.selected = true;
				} else {
					op.selected = false;
				}
			});
			this.setState(
				{
					currentQuestion: currentQuestion,
				},
				() => {
					this.handleValidateAnswer();
				}
			);
		} else if (currentQuestion.choice === "multiple") {
			currentQuestion.options.map((op) => {
				if (op.index === option.index) {
					op.selected = !op.selected;
				}
			});
			this.setState(
				{
					currentQuestion: currentQuestion,
				},
				() => {
					this.handleValidateAnswer();
				}
			);
		}
	};

	handleValidateAnswer = () => {
		var success = 0;
		var selected = 0;

		this.state.currentQuestion.options.map((option) => {
			if (option.answer === true && option.answer === option.selected) {
				success += 1;
			}
			if (option.selected) {
				selected += 1;
			}
		});
		//Check for wrong answer
		if (success === 0 || success !== selected) {
			if (this.state["attemptedSection" + this.state.currentSecIndex].includes(this.state.currentQuestion)) {
				var newArray = this.state["attemptedSection" + this.state.currentSecIndex].filter((item) => item.index !== this.state.currentQuestion.index);
				newArray.push(this.state.currentQuestion);
				this.setState(
					{
						["attemptedSection" + this.state.currentSecIndex]: newArray,
					});
			} else {
				this.setState(
					{
						["attemptedSection" + this.state.currentSecIndex]: [...this.state["attemptedSection" + this.state.currentSecIndex], this.state.currentQuestion],
					});
			}
		}

		// Check for correct answer
		else {
			if (this.state["attemptedSection" + this.state.currentSecIndex].includes(this.state.currentQuestion)) {
				var newArray = this.state["attemptedSection" + this.state.currentSecIndex].filter((item) => item.index !== this.state.currentQuestion.index);
				newArray.push(this.state.currentQuestion);
				this.setState(
					{
						["attemptedSection" + this.state.currentSecIndex]: newArray,
					});
			} else {
				this.setState(
					{
						["attemptedSection" + this.state.currentSecIndex]: [...this.state["attemptedSection" + this.state.currentSecIndex], this.state.currentQuestion],
						
					});
			}
		}
	};

	handleEndExam = async() => {
		this.setState(
			{
				isEnd: true,
			},
			async() => {
				// Calculating total Marks
				clearInterval(timer);
				var check = await this.handleTotalMarks();
				if (check === 'Done') {
					var quesArray = [];
					for(var i = 0; i < this.props.exam.questions.length; i++){
						var newSection = {};
						newSection.name = this.props.exam.questions[i].name;
						newSection.questions = this.state["attemptedSection" + i];

						quesArray.push(newSection);
					}

					// Storing exam result data
					const data = {
						studentId : this.props.studentId,
						examId : this.props.exam._id,
						schoolId : this.props.schoolId,
						questions: quesArray,
						title : this.props.exam.title,
						marks: this.state.totalMarks,
						correct: this.state.totalCorrect,
						wrong: this.state.totalMarks,
						subject:this.props.exam.subjectName,
					}

					const check2 = await this.props.saveExam(data);
					if (check2 !== null) {
						console.log(check2);
					}
				}
			}
		);
	};

	handleTotalMarks = () => {
		var total = 0, correct = 0, totalQues = 0;
		for (var i = 0; i < this.props.exam.questions.length; i++) {
			this.state["attemptedSection" + i].map((question) => {
				totalQues = totalQues + 1;
				question.options.map((option) => {
					if (option.answer === true && option.selected === true) {
						total = total + parseInt(question.marks);
						correct = correct + 1;
					}
				});
			});
		}

		this.setState({
			totalMarks: total,
			totalCorrect: correct,
			totalWrong: totalQues - correct
		});

		return "Done";
	};

	render() {
		return (
			<div className='attempt-exam-container'>
				{this.state.loading ? null : (
					<>
						{this.state.isEnd ? (
							<div className='thankyou-board'>
								<h3>You've successfully taken the examination.</h3>
								<Lottie options={{ animationData: thankyou, loop: false }} width={400} height={400} style={{ margin: 0 }} />
								<h4>Percentage : {((this.state.totalMarks / this.state.actualMarks) * 100).toFixed(2)}%</h4>
								<h2>Total Marks Scored : {this.state.totalMarks}</h2>
								<p>You're marks obtained will reflect on your grade book.</p>
							</div>
						) : (
							<>
								<div className='question-board'>
									<div className='question-box'>
										<h1>
											Section : {this.state.currentQuestion.section} / Code : {this.state.currentQuestion.code}
										</h1>
										<MathJax math={this.state.currentQuestion.question} />
										<div className='box-options'>
											{this.state["attemptedSection" + this.state.currentSecIndex].includes(this.state.currentQuestion) ? (
												<>
													{this.state["attemptedSection" + this.state.currentSecIndex].map((item) => {
														if (item.index === this.state.currentQuestion.index) {
															return (
																<>
																	{item.options.map((option, index) => {
																		return (
																			<div>
																				<input
																					type='radio'
																					name='options'
																					id={"option" + index}
																					style={{ display: "none" }}
																					onChange={() => this.handleGetAnswer(option)}
																				/>
																				<label htmlFor={"option" + index} className='options'>
																					{option.selected ? (
																						<div className='option-overlay'>
																							<img src={check} alt='check' />
																						</div>
																					) : null}
																					<MathJax math={option.value} />
																				</label>
																			</div>
																		);
																	})}
																</>
															);
														}
													})}
												</>
											) : (
												<>
													{this.state.currentQuestion.options.length > 0 &&
														this.state.currentQuestion.options.map((option, index) => {
															return (
																<>
																	<input
																		type='radio'
																		name='options'
																		id={"option" + index}
																		style={{ display: "none" }}
																		onChange={() => this.handleGetAnswer(option)}
																	/>
																	<label htmlFor={"option" + index} className='options'>
																		{option.selected ? (
																			<div className='option-overlay'>
																				<img src={check} alt='check' />
																			</div>
																		) : null}
																		<MathJax math={option.value} />
																	</label>
																</>
															);
														})}
												</>
											)}
										</div>
										<div className='button-options'>
											{this.state.currentIndex === this.state["section" + this.state.currentSecIndex].length - 1 &&
											this.state.currentSecIndex === this.props.exam.questions.length - 1 ? null : (
												<button id='submit' type='submit' onClick={this.handleNext}>
													Next
												</button>
											)}
										</div>
									</div>
								</div>
								<div className='stats-board'>
									<div className='timer-box'>
										<p>Time Remaining</p>
										<h1>{<DateCountdown dateTo={moment(new Date()).add(parseInt(this.props.exam.duration), "m").toDate()} callback={this.handleEndExam} />}</h1>
									</div>
									<h1>Exam Sections</h1>
									{
										//Section Mapping
										this.state.questions.length > 0 &&
											this.state.questions.map((item, index) => {
												return (
													<div className='section-box'>
														<div className='section-box-head' onClick={() => this.setState({ tab: index })}>
															<p>{item.name}</p>
															<p>
																<RiArrowDownSLine />
															</p>
														</div>
														<div className={this.state.tab === index ? "section-box-body active" : "section-box-body"}>
															<div className='question-list'>
																{
																	//Question index mapping
																	item.questions.length > 0 &&
																		item.questions.map((ques, key) => {
																			return (
																				<div className='question-circle' onClick={() => this.handleSetCurrentQuestion(ques, key, index)}>
																					<p>{key + 1}</p>
																				</div>
																			);
																		})
																}
															</div>
														</div>
													</div>
												);
											})
									}
									<div className='button-div'>
										<button onClick={() => this.setState({ showModal: true })}>End Exam</button>
									</div>
								</div>

								<Modal show={this.state.showModal} onHide={() => this.setState({ showModal: false })} backdrop='static' keyboard={false}>
									<Modal.Header closeButton>
										<Modal.Title>Confirm</Modal.Title>
									</Modal.Header>
									<Modal.Body>Are you sure ?</Modal.Body>
									<Modal.Footer>
										<Button variant='secondary' onClick={() => this.setState({ showModal: false })}>
											Cancel
										</Button>
										<Button variant='primary' onClick={this.handleEndExam}>
											Yes
										</Button>
									</Modal.Footer>
								</Modal>
							</>
						)}
					</>
				)}
			</div>
		);
	}
}

function mapDispatchToProps(dispatch) {
    return {
        saveExam: (data) => dispatch(saveExam(data)),
    }
}

export default connect(null, mapDispatchToProps)(AttemptExam);
