import React from "react";

import "./creativity.css";

import { Form } from "react-bootstrap";

import moment from "moment";
import Chart from "react-apexcharts";

import Lottie from "lottie-react-web";
import empty from '../../../../assets/EmptyFolder.json';

class MyCreativity extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			subject: "",
			format: "",
			date: "",
			month: "",
			creativityArray: [],
			filteredArray: [],
			creativities: [],
			series1: [0, 0, 0, 0],
			options1: {
				chart: {
					width: 300,
					type: "pie",
				},
				labels: [
					"synthesize ideas in original and surprising ways",
					"ask new questions to build upon an idea",
					"brainstorm multiple ideas and solutions to problems",
					"communicate ideas in new and innovative ways",
				],
				legend: {
					show: false,
				},
				responsive: [
					{
						breakpoint: 480,
						options: {
							chart: {
								width: 200,
							},
							legend: {
								position: "bottom",
							},
						},
					},
				],
			},
		};
	}

	componentDidMount = () => {
		this.setState({
			creativityArray: this.props.creativity,
			loading: false,
		});
	};

	handleSetSubject = (e) => {
		this.setState({
			subject: e.target.value,
		});
	};

	handleFormat = (e) => {
		if (this.state.subject === "") {
			alert("Please select subject");
		} else {
			this.setState({
				format: e.target.value,
			});
		}
	};

	handleSetDate = (e) => {
		this.setState(
			{
				date: e.target.value,
			},
			() => {
				this.handleFilter("Day");
			}
		);
	};

	handleSetMonth = (e) => {
		this.setState(
			{
				month: e.target.value,
			},
			() => {
				this.handleFilter("Month");
			}
		);
	};

	handleFilter = (format) => {
		var c1 = 0,
			c2 = 0,
			c3 = 0,
			c4 = 0;
		var filteredArray = [],
			creativities = [];

		if (format === "Day") {
			this.state.creativityArray.map((item) => {
				if (item.date === this.state.date) {
					filteredArray.push(item);
				}
			});
		}

		if (format === "Month") {
			this.state.creativityArray.map((item) => {
				if (item.date.substring(0, item.date.length - 3) === this.state.month) {
					filteredArray.push(item);
				}
			});
		}

		filteredArray.map((item) => {
			var x = {};
			x.date = item.date;
			x.creativity = item.status;
			creativities.push(x);
		});
		creativities.map((c) => {
			if (c.creativity.includes("synthesize ideas in original and surprising ways")) {
				c1 += 1;
			}
			if (c.creativity.includes("ask new questions to build upon an idea")) {
				c2 += 1;
			}
			if (c.creativity.includes("brainstorm multiple ideas and solutions to problems")) {
				c3 += 1;
			}
			if (c.creativity.includes("communicate ideas in new and innovative ways")) {
				c4 += 1;
			}
		});
		this.setState(
			{
				creativities: creativities,
				series1: [c1, c2, c3, c4],
			},
			() => {
				console.log(this.state.series1);
			}
		);
	};

	render() {
		return (
			<div className='my-creativity-container'>
				<div className='select-sub'>
					<Form.Group className='container-fluid'>
						<Form.Label>Select Subject</Form.Label>
						<Form.Control as='select' name='subject' onChange={this.handleSetSubject}>
							<option selected hidden>
								Choose...
							</option>
							<option value='Mathematics'>Mathematics</option>
							<option value='Science'>Science</option>
							<option value='English'>English</option>
							<option value='Islamic Studies'>Islamic Studies</option>
							<option value='Social Studies'>Social Studies</option>
							<option value='Information Technology'>Information Technology</option>
							<option value='Life Skills'>Life Skills</option>
							<option value='Music'>Music</option>
							<option value='Sports'>Sports</option>
							<option value='Arts'>Arts</option>
						</Form.Control>
					</Form.Group>
				</div>

				<div className='select-sub'>
					<Form.Group className='container-fluid'>
						<Form.Label>Select Format</Form.Label>
						<div key='inline-radio'>
							<Form.Check inline label='Day' type='radio' name='format' value='Day' onChange={this.handleFormat} />
							<Form.Check inline label='Monthly' type='radio' name='format' value='Monthly' onChange={this.handleFormat} />
						</div>
					</Form.Group>
				</div>

				{/* Select Day */}
				{this.state.format === "Day" ? (
					<Form.Group className='container-fluid'>
						<Form.Label>Select Date</Form.Label>
						<Form.Control type='date' name='date' onChange={this.handleSetDate} />
					</Form.Group>
				) : null}

				{/* Select Month */}
				{this.state.format === "Monthly" ? (
					<Form.Group className='container-fluid'>
						<Form.Label>Select Month</Form.Label>
						<Form.Control type='month' name='month' onChange={this.handleSetMonth} />
					</Form.Group>
				) : null}

				{this.state.creativities.length > 0 ? (
					<div className='graph'>
						<Chart options={this.state.options1} series={this.state.series1} type='pie' style={{ width: "100%", height: "100%" }} />
					</div>
				) : null}

				<div className='behaviour-chart'>
					{
						this.state.creativities && this.state.creativities.length > 0
						?
						<>
						{
							this.state.creativities.map((item) => (
								<div className='behaviour-card'>
									<div className='behaviour-date'>
										<p>{moment(item.date).format("LL")}</p>
									</div>
									<div className='behaviour-list'>
										<div className={item.creativity.includes("synthesize ideas in original and surprising ways") ? "behaviour active" : "behaviour"}>
											<i class='fas fa-check-circle'></i>
											<p>synthesize ideas in original and surprising ways</p>
										</div>
										<div className={item.creativity.includes("ask new questions to build upon an idea") ? "behaviour active" : "behaviour"}>
											<i class='fas fa-check-circle'></i>
											<p>ask new questions to build upon an idea</p>
										</div>
										<div className={item.creativity.includes("brainstorm multiple ideas and solutions to problems") ? "behaviour active" : "behaviour"}>
											<i class='fas fa-check-circle'></i>
											<p>brainstorm multiple ideas and solutions to problems</p>
										</div>
										<div className={item.creativity.includes("communicate ideas in new and innovative ways") ? "behaviour active" : "behaviour"}>
											<i class='fas fa-check-circle'></i>
											<p>communicate ideas in new and innovative ways</p>
										</div>
									</div>
								</div>
							))
						}
						</>
						:
						<div style={{display : 'flex', flexDirection : 'column', alignItems : 'center'}}>
							<Lottie options={{ animationData: empty }} height = {300} width = {300}/>
							<p>No creativity found</p>
						</div>
					}
				</div>
			</div>
		);
	}
}

export default MyCreativity;
