import React from "react";

import "./attendance.style.css";

import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import Chart from "react-apexcharts";
import { Form, Col } from "react-bootstrap";

class MyAttendance extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			subject: "",
			showCalendar: false,
			showGraph: false,
			series1: [0, 0],
			options1: {
				chart: {
					width: 300,
					type: "pie",
				},
				labels: ["Present", "Absent"],
				legend: {
					show: false,
				},
				responsive: [
					{
						breakpoint: 480,
						options: {
							chart: {
								width: 200,
							},
							legend: {
								position: "bottom",
							},
						},
					},
				],
			},
		};
	}

	handleSetSubject = (e) => {
		var arrayDates = [];
		var c1 = 0,
			c2 = 0;
		this.props.attendance.map((item) => {
			if (item.subjectName === e.target.value) {
				arrayDates.push(item);
				if (item.date.split("-")[1] === (new Date().getMonth() + 1).toString() && item.date.split("-")[0] === new Date().getFullYear().toString()) {
					if (item.status === "Present") {
						c1 += 1;
					}
					if (item.status === "Absent") {
						c2 += 1;
					}
				}
			}
		});
		this.setState(
			{
				subject: e.target.value,
				showCalendar: !true,
				series1: [c1, c2],
			},
			() => {
				var zeros = 0;
				this.state.series1.map((s) => {
					if (s === 0) {
						zeros += 1;
					}
				});
				this.setState({
					showCalendar: true,
					showGraph: zeros > 0 ? false : true,
				});
			}
		);
	};

	handleTileContent = (a) => {
		var arrayDates = [];
		this.props.attendance.map((item) => {
			if (item.subjectName === this.state.subject) {
				arrayDates.push(item);
			}
		});
		var match = false;
		arrayDates.map((x) => {
			var date1 = new Date(a.date).getFullYear() + "-" + new Date(a.date).getMonth() + "-" + new Date(a.date).getDate();
			if (x.date === date1) {
				match = x;
			}
		});
		if (match) {
			if (match.status === "Present") {
				return <p style={{ padding: "0.5rem 0", background: "green", color: "#fff", borderRadius: "5px" }}>{match.status}</p>;
			}
			if (match.status === "Absent") {
				return <p style={{ padding: "0.5rem 0", background: "red", color: "#fff", borderRadius: "5px" }}>{match.status}</p>;
			}
		} else {
			return null;
		}
	};

	handleChangeCalender = ({ activeStartDate, value, view }) => {
		var c1 = 0,
			c2 = 0;
		this.props.attendance.map((item) => {
			if (item.subjectName === this.state.subject) {
				if (item.date.split("-")[1] === new Date(activeStartDate).getMonth().toString() && item.date.split("-")[0] === new Date(activeStartDate).getFullYear().toString()) {
					if (item.status === "Present") {
						c1 += 1;
					}
					if (item.status === "Absent") {
						c2 += 1;
					}
				}
			}
		});
		this.setState(
			{
				series1: [c1, c2],
			},
			() => {
				var zeros = 0;
				this.state.series1.map((s) => {
					if (s === 0) {
						zeros += 1;
					}
				});
				this.setState({
					showGraph: zeros > 0 ? false : true,
				});
			}
		);
	};

	render() {
		return (
			<div className='my-attendance-container'>
				<div className='attendance-wrapper'>
					<div className='select-sub'>
						<Form.Group className='container-fluid'>
							<Form.Label>Select Subject</Form.Label>
							<Form.Control as='select' name='subject' onChange={this.handleSetSubject}>
								<option selected hidden>
									Choose...
								</option>
								<option value='Mathematics'>Mathematics</option>
								<option value='Science'>Science</option>
								<option value='English'>English</option>
								<option value='Islamic Studies'>Islamic Studies</option>
								<option value='Social Studies'>Social Studies</option>
								<option value='Information Technology'>Information Technology</option>
								<option value='Life Skills'>Life Skills</option>
								<option value='Music'>Music</option>
								<option value='Sports'>Sports</option>
								<option value='Arts'>Arts</option>
							</Form.Control>
						</Form.Group>
					</div>
					{this.state.showGraph ? (
						<div className='graph'>
							<Chart options={this.state.options1} series={this.state.series1} type='pie' style={{ width: "100%", height: "100%" }} />
						</div>
					) : null}
					<div className='show-calendar'>
						{this.state.showCalendar ? (
							<Calendar tileContent={this.handleTileContent} maxDate={new Date()} onActiveStartDateChange={this.handleChangeCalender} onClickMonth={this.handleChangeCalender} />
						) : null}
					</div>
				</div>
			</div>
		);
	}
}

export default MyAttendance;
