import React from "react";
import { connect } from "react-redux";
import IIGPNav from "./components/nav/nav";

import { Redirect } from "react-router-dom";
import "./iigpHome.css";
import Footer from "./components/footer/footer";
import TopNav from "./components/topnav/topnav";

import plant from './assets/plant.png';
import globe from './assets/globe.png';
import earth from './assets/earth.png';
import bookshelf from './assets/bookshelf.png';
import women from './assets/women.png';
import books from './assets/books.png';
import teacher from './assets/teacher.png';
import student from './assets/student.png';
import image3 from './assets/image3-min.png';
import wifi from './assets/wifi.png';

import image5 from './assets/image5-min.png';
import image6 from './assets/image6-min.png';
import image7 from './assets/image7-min.png';

import icon1 from './assets/icon1.png';
import icon2 from './assets/icon2.png';
import icon3 from './assets/icon3.png';

import bg from './assets/course_bg-2.png';
import testimonial from './assets/testimonial.png';

class IIGPHome extends React.Component {
	render() {
		if (this.props.auth.currentUser) {
			return <Redirect to='/dashboard' />;
		}
		return (
			<div className='iigp-home-container'>
				<TopNav />
				<IIGPNav />
				<div className='iigp-ads-banner'>
					<div className='ads-content'>
						<h1>We Take Learning to New Heights</h1>
						<p>We believe everyone has the capacity to be creative. IIGP is a place</p>
						<p>where people develop their own potential.</p>
						<button>Learn more</button>
					</div>
					<div className = "ads-images">
						<img src={plant} className="ad-plant"/>
						<img src={globe} className = "ad-globe"/>
						<img src={earth} className = "ad-earth"/>
						<img src={bookshelf} className = "ad-bookshelf"/>
						<img src={women} className = "ad-women"/>
						<img src={books} className = "ad-books"/>
						<img src={teacher} className = "ad-teacher"/>
						<img src={student} className = "ad-student"/>
						<img src={image3} className = "ad-image3"/>
						<img src={wifi} className = "ad-wifi"/>
						<img src={image5} className = "ad-image5"/>
						<img src={image6} className = "ad-image6"/>
						<img src={image7} className = "ad-image7"/>
					</div>
				</div>
				<div className = "iigp-about">
					<div className = "iigp-about-left">
						<h1>We are IIGP.<br/>An online learning community</h1>
						<div>
							<p>At IIGP, we believe everyone should have the opportunity to create progress through technology and develop the skills of tomorrow. With assessments, learning paths and courses authored by industry experts.</p>
						</div>
					</div>
					<div className = "iigp-about-right">
						<div className = "iigp-about-box">
							<img src={icon1}/>
							<h3>5061+</h3>
							<p>Courses</p>
						</div>
						<div className = "iigp-about-box">
							<img src={icon2}/>
							<h3>Award</h3>
							<p>Winner</p>
						</div>
						<div className = "iigp-about-box">
							<img src={icon3}/>
							<h3>15000+</h3>
							<p>Students</p>
						</div>
					</div>
				</div>
				<div className = "iigp-feature">
					<img src={bg}/>
				</div>
				<div className = "iigp-testimonial">
					<img src={testimonial}/>
				</div>
				<Footer />
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth,
	};
}

export default connect(mapStateToProps)(IIGPHome);
