import axios from 'axios';

import { 
    FETCH_STUDENT_PROFILE, 
    SAVE_STUDENT_RESULT_ERR, 
    SAVE_STUDENT_RESULT_SUCC,
    SAVE_STUDENT_EXAM_ERR,
    SAVE_STUDENT_EXAM_SUCC
} from './studentTypes';

export const fetch_student_profile_data = (studentData) => async (dispatch) => {
    const res = await axios.post("/api/student/profile", studentData);
    dispatch({ type: FETCH_STUDENT_PROFILE, payload: res.data });
    return res.data;
}

//SAVING PRACTICE
export const saveResult = (data) => async (dispatch) => {
    const res = await axios.post("/api/practice/saveresult", data);
    console.log(res, data);
    if (res.data.type === "error") {
        dispatch({ type: SAVE_STUDENT_RESULT_ERR, payload: res.data });
        return res.data
    } else {
        dispatch({ type: SAVE_STUDENT_RESULT_SUCC, payload: res.data });
        return res.data;
    }
}

// SAVING EXAM ATTEMPTED

export const saveExam = (data) => async(dispatch) => {
    const res = await axios.post("/api/exam/saveExam", data);
    if (res.data.type === "error") {
        dispatch({type: SAVE_STUDENT_EXAM_ERR, payload: res.data});
    }
    else {
        dispatch({ type: SAVE_STUDENT_EXAM_SUCC, payload: res.data });
        return res.data;
    }
}

// DISCUSSION

export const addMessage = (data) => async (dispatch) => {
    const res = await axios.post("/api/myschool/mydiscussion/addMessage", data);
    if (res.data.type === "success") {
        var data2 = {
            studentId: data.userId,
        };
        const res2 = await axios.post("/api/student/profile", data2);
        dispatch({ type: FETCH_STUDENT_PROFILE, payload: res2.data });
        return res.data;
    }
};

