import React from "react";

import "./exams.css";

import { Form, Col } from "react-bootstrap";
import SingleExam from "./singleExam/singleExam";
import AttemptExam from "./attemptExam/attemptExam";

import Lottie from "lottie-react-web";
import empty from '../../assets/EmptyFolder.json';

class MyExams extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			examsArray: [],
            filteredArray: [],
            selectedExam : null,
            startExam : false
		};
	}

	componentDidMount = () => {
		this.setState({
			examsArray: this.props.student.exams,
			filteredArray: this.props.student.exams,
			loading: false,
		});
	};

	handleSetSubject = (e) => {
		var newArray = [];
		this.state.examsArray.map((item) => {
			if (item.subjectName === e.target.value) {
				newArray.push(item);
			}
		});

		this.setState({
			filteredArray: newArray,
		});
    };
    
    handlestartExam = (value1, value2) => {
        this.setState({
            selectedExam: value1,
            startExam : value2
        })
    }

	render() {
		return (
			<div className={this.state.startExam ? 'my-exams-container' : 'my-exams-container add-padding'}>
                {
                    this.state.loading 
                    ? 
                    null 
                    : 
						<>
				            {
                                this.state.startExam
                                ?
                                <AttemptExam exam = {this.state.selectedExam} studentId = {this.props.student._id} schoolId = {this.props.student.school_id}/>
                                :
                                <div className='my-exams-wrapper'>
                                    <div className='select-sub'>
                                        <Form.Group className='container-fluid'>
                                            <Form.Label>Select Subject</Form.Label>
                                            <Form.Control as='select' name='subject' onChange={this.handleSetSubject}>
                                                <option selected hidden>Choose...</option>
                                                <option value="Mathematics">Mathematics</option>
                                                <option value="Science">Science</option>
                                                <option value="English">English</option>
                                                <option value="Islamic Studies">Islamic Studies</option>
                                                <option value="Social Studies">Social Studies</option>
                                                <option value="Information Technology">Information Technology</option>
                                                <option value="Life Skills">Life Skills</option>
                                                <option value="Music">Music</option>
                                                <option value="Sports">Sports</option>
                                                <option value="Arts">Arts</option>
                                            </Form.Control>
                                        </Form.Group>
                                    </div>
                                    {
                                        this.state.filteredArray.length > 0 
                                        ? 
                                            <>
                                                <div className='exam-list'>
                                                    {this.state.filteredArray.map((item) => {
                                                        return <SingleExam exam = {item} studentId = {this.props.student._id} sendExamData = {(value1, value2) => this.handlestartExam(value1, value2)}/>;
                                                    })}
                                                </div>
                                            </>
                                        : 
                                        <>
                                            <div className = "exam-list">
                                                <Lottie options={{ animationData: empty }} height = {300} width = {300}/>
                                                <p>No exam found</p>
                                            </div>
                                        </>
                                    }
                                </div>
                            }
						</>
                }
			</div>
		);
	}
}

export default MyExams;

