import React from "react";
import { Button, Form } from "react-bootstrap";
import "./style.css";
import { connect } from "react-redux";
import { loginUser } from "../../redux/actions/authActions";
import { Redirect } from "react-router-dom";
import { Spinner } from "react-bootstrap";
import ico from "../IIGPHome/assets/iigp.png";
import student from "../IIGPHome/assets/xyz.png";
import Footer from "../IIGPHome/components/footer/footer";
import TopNav from "../IIGPHome/components/topnav/topnav";
import IIGPNav from "../IIGPHome/components/nav/nav";
import StudentFooter from "../studentWebsite/websiteComponents/footer/footer";

class Login extends React.Component {
	constructor() {
		super();
		this.state = {
			email: "",
			password: "",
			loading: false,
		};
	}

	handleSubmit = () => {
		var email = this.state.email;
		var password = this.state.password;
		const userData = {
			email,
			password,
		};
		this.props.loginUser(userData);
		this.setState({
			loading: true,
		});
	};

	render() {
		if (this.props.auth.currentUser) {
			return <Redirect to='/dashboard' />;
		}
		return (
			<>
				<div className='background-box'>
					<div className='login-box'>
						<TopNav />
						<IIGPNav />
						<div className='login-cont'>
							<img src={student} alt='img' />
							<div className='Login'>
								<p>Welcome Back</p>
								<Form>
									<Form.Group controlId='formBasicEmail'>
										{/* <Form.Label>Email address</Form.Label> */}
										<Form.Control
											type='email'
											placeholder='Enter email'
											onChange={(e) => {
												this.setState({ email: e.target.value });
											}}
										/>
									</Form.Group>

									<Form.Group controlId='formBasicPassword'>
										{/* <Form.Label>Password</Form.Label> */}
										<Form.Control
											type='password'
											placeholder='Password'
											onChange={(e) => {
												this.setState({ password: e.target.value });
											}}
										/>
										<a href=''>forgot password ?</a>
									</Form.Group>
									<div
										style={{
											width: "100%",
											display: "flex",
											justifyContent: "center",
										}}>
										{this.state.loading ? (
											<Spinner animation='border' role='status' variant='info'>
												<span className='sr-only'>Loading...</span>
											</Spinner>
										) : (
											<Button variant='primary' type='button' onClick={this.handleSubmit}>
												Submit
											</Button>
										)}
									</div>
									<div className='about'>
										<a href=''>Terms and conditions</a>
										<a href=''>Policiy</a>
									</div>
								</Form>
							</div>
						</div>
						{/* <div className="text-login">
					<p>School Management</p>
					<h5>For a improved learning and teaching experience ! </h5>
					</div> */}
					</div>
					<StudentFooter />
				</div>
			</>
		);
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		loginUser: (userData) => dispatch(loginUser(userData)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(Login);
