import React from "react";

import "./singleLab.css";

import physicsIcon from "./assets/electronic-circuit.svg";
import chemIcon from "./assets/flasks.svg";
import bioIcon from "./assets/bacteria.svg";
import compIcon from "./assets/responsive.svg";

import { Modal, Form } from "react-bootstrap";

import moment from "moment";

class SingleLab extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			showBtn: !false,
			showRemark: false,
			remark: "",
			observation: "",
		};
	}

	componentDidMount = () => {
		this.props.lab.attend.length > 0 &&
			this.props.lab.attend.map((item) => {
				if (item.id === this.props.studentId) {
					this.setState({
						showBtn: true,
						remark: item.remark,
						observation: item.observation,
					});
				}
			});

		this.setState({
			loading: false,
		});
	};

	render() {
		return (
			<div className='single-lab-container'>
				{this.state.loading ? null : (
					<>
						{this.props.lab.subjectName === "Physics" ? (
							<img src={physicsIcon} alt='icon' />
						) : (
							<>
								{this.props.lab.subjectName === "Chemistry" ? (
									<img src={chemIcon} alt='icon' />
								) : (
									<>
										{this.props.lab.subjectName === "Biology" ? (
											<img src={bioIcon} alt='icon' />
										) : (
											<img src={compIcon} alt='icon' />
										)}
									</>
								)}
							</>
						)}

						<div className='assignment-head'>
							<p>{this.props.lab.title}</p>
							<p className='objectives'>{this.props.lab.objectives}</p>
							<h5>Subject : {this.props.lab.subjectName}</h5>
							<span>
								Start Date : <i>{moment(this.props.lab.startDate).format("LL")}</i>
							</span>
							<span>
								End Date : <i>{moment(this.props.lab.endDate).format("LL")}</i>
							</span>
							{this.state.showBtn ? (
								<button onClick={() => this.setState({ showRemark: true })}>Click to view report</button>
							) : (
								<span style={{ color: "red" }}>You have not attended the lab yet</span>
							)}
						</div>

						<Modal
							show={this.state.showRemark}
							onHide={() => this.setState({ showRemark: false })}
							backdrop='static'
							keyboard={false}
							aria-labelledby='contained-modal-title-vcenter'
							centered>
							<Modal.Header closeButton>
								<Modal.Title>Lab Report</Modal.Title>
							</Modal.Header>
							<Modal.Body>
								<Form.Group controlId='exampleForm.ControlInput1'>
									<Form.Label>Remark</Form.Label>
									<Form.Control value={this.state.remark} disabled />
								</Form.Group>
								<Form.Group controlId='exampleForm.ControlTextarea1'>
									<Form.Label>Observations</Form.Label>
									<Form.Control as='textarea' rows={3} value={this.state.observation} disabled />
								</Form.Group>
							</Modal.Body>
						</Modal>
					</>
				)}
			</div>
		);
	}
}

export default SingleLab;
