import React from 'react';

import './scores.css';

import JuicerLoading from '../juicerLoading/juicerLoading';

import {TiArrowSortedDown} from 'react-icons/ti';

import moment from 'moment';

class ScoresList extends React.Component {
    constructor(props){
        super(props);
        this.accordionContent =[];
        this.state = {
            loading : false,
            filteredData : [],
        }
    }

    componentDidMount() {

        var visited = [], filteredData = [];
        this.props.Adata.map((item) => {
            if (visited.includes(item.chapter) === false) {
                var chap = {}, topicDetails = {};
                chap.chapterName = item.chapter;
                chap.topic = [];
                topicDetails.topicName = item.topic;
                topicDetails.score = item.score;
                topicDetails.totalQues = item.correct + item.wrong;
                topicDetails.duration = parseInt(item.duration);
                topicDetails.date = item.date;
                chap.topic.push(topicDetails);
                
                filteredData.push(chap);
                visited.push(item.chapter);
            }
            else {
                filteredData.map((x) => {
                    var topicDetails = {};
                    if (x.chapterName === item.chapter) {
                        topicDetails.topicName = item.topic;
                        topicDetails.score = item.score;
                        topicDetails.totalQues = item.correct + item.wrong;
                        topicDetails.duration = parseInt(item.duration);
                        topicDetails.date = item.date;

                        x.topic.push(topicDetails);
                    }
                })
            }
        })
        this.setState({
            filteredData : filteredData,
            loading : false
        })
    }

    handleAcc = (e) => {
        var panel =this.accordionContent[e].nextElementSibling;
        if (panel.style.display === "flex") 
        {
            panel.style.display = "none";
        } 
        else 
        {
            panel.style.display = "flex";
        }
    }

    render() {
        return(
            <div className = "scores-container">
                {
                    this.state.loading
                    ?
                    <JuicerLoading />
                    :
                    <div className = "scores-board">
                        <div className = "scores-head">
                            <h1>SCORES HISTORY</h1>
                        </div>
                        <div className = "scores-body">
                            <div className = "scores-table-head">
                                <div className = "table-head-left">
                                    <p>SKILL</p>
                                </div>
                                <div className = "table-head-right">
                                    <div className = "score">
                                        <p>SCORE</p>
                                    </div>
                                    <div className = "questions">
                                        <p>QUESTIONS</p>
                                    </div>
                                    <div className = "time-taken">
                                        <p>TIME SPENT</p>
                                    </div>
                                    <div className = "last-practiced">
                                        <p>LAST PRACTICED</p>
                                    </div>
                                </div>
                            </div>
                            <div className = "scores-table-body">
                                {
                                    this.state.filteredData && this.state.filteredData.map((item,index) => {
                                        return (
                                            <div className= "scores-accordion">
                                                <div class="accordion" ref={accordionContent => this.accordionContent[index] = accordionContent} onClick={()=>this.handleAcc(index)}>
                                                    <h1><TiArrowSortedDown /></h1>
                                                    <p>{item.chapterName}</p>
                                                </div>
                                                <div className = "panel">
                                                    {
                                                        item.topic.map((i) => {
                                                            return(
                                                                <div className = "stat-panel">
                                                                    <div className = "panel-left">
                                                                        <p>{i.topicName}</p>
                                                                    </div>
                                                                    <div className = "panel-right">
                                                                        <div className = "score">
                                                                            <p>{i.score}</p>
                                                                        </div>
                                                                        <div className = "questions">
                                                                            <p>{i.totalQues}</p>
                                                                        </div>
                                                                        <div className = "time-taken">
                                                                            <p>{Math.round(i.duration / 60)} mins</p>
                                                                        </div>
                                                                        <div className = "last-practiced">
                                                                            <p>{moment(i.date).format('LLL')}</p>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            )
                                                        })
                                                    }
                                                </div>
                                            </div>
                                        )
                                    })
                                }

                            </div>
                        </div>
                    </div>
                }
            </div>
        )
    }
}

export default ScoresList;