import React from "react";

import "./labs.style.css";

import { Form, Col } from "react-bootstrap";
import SingleLab from "./singleLab/singleLab";

import Lottie from "lottie-react-web";
import empty from '../../assets/EmptyFolder.json';

class MyLabs extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			labsArray: [],
			filteredArray: [],
		};
	}

	componentDidMount = () => {
		this.setState({
			labsArray: this.props.student.labs,
			filteredArray: this.props.student.labs,
			loading: false,
		});
	};

	handleSetSubject = (e) => {
		var newArray = [];
		this.state.labsArray.map((item) => {
			if (item.subjectName === e.target.value) {
				newArray.push(item);
			}
		});

		this.setState({
			filteredArray: newArray,
		});
	};

	render() {
		return (
			<div className='my-labs-container'>
				<div className='my-labs-wrapper'>
					{this.state.loading ? null : (
						<>
							<div className='select-sub'>
								<Form.Group className='container-fluid'>
									<Form.Label>Select Subject</Form.Label>
									<Form.Control as='select' name='subject' onChange={this.handleSetSubject}>
										<option selected hidden>
											Choose...
										</option>
										<option value='Physics'>Physics</option>
										<option value='Chemistry'>Chemistry</option>
										<option value='Biology'>Biology</option>
										<option value='Music'>Music</option>
										<option value='Sports'>Sports</option>
										<option value='Arts'>Arts</option>
										<option value='Information Technology'>Information Technology</option>
									</Form.Control>
								</Form.Group>
							</div>
							{this.state.filteredArray.length > 0 ? (
								<>
									<div className='lab-list'>
										{this.state.filteredArray.map((item) => {
											return <SingleLab lab={item} studentId={this.props.student._id} />;
										})}
									</div>
								</>
							) : (
								<div className='lab-list'>
									<Lottie options={{ animationData: empty }} height = {300} width = {300}/>
									<p>No labs found</p>
								</div>
							)}
						</>
					)}
				</div>
			</div>
		);
	}
}

export default MyLabs;
