import React from "react";

import "./gradeBook.css";
import MyAssignmentGrade from "./sections/assignment/assignmentGrade";
import MyExamGrade from "./sections/exam/examGrade";
import MyHomeworkGrade from "./sections/homework/homeworkGrade";
import MyLabworkGrade from "./sections/labwork/labworkGrade";

class MyGradeBook extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			tab: "Exams",
		};
	}

	handleSetTab = (e) => {
		this.setState(
			{
				tab: e,
			},
			() => {
				this.props.setSubTab(e);
			}
		);
	};

	render() {
		return (
			<div className='my-gradebook-container'>
				<div className='gradebook-wrapper'>
					<div className='gradebook-tab'>
						<div className={this.state.tab === "Exams" ? "tab active" : "tab"} onClick={() => this.handleSetTab("Exams")}>
							<p>Exams</p>
						</div>
						<div className={this.state.tab === "Assignments" ? "tab active" : "tab"} onClick={() => this.handleSetTab("Assignments")}>
							<p>Assignments</p>
						</div>
						<div className={this.state.tab === "Homework" ? "tab active" : "tab"} onClick={() => this.handleSetTab("Homework")}>
							<p>Homework</p>
						</div>
						<div className={this.state.tab === "Labwork" ? "tab active" : "tab"} onClick={() => this.handleSetTab("Labwork")}>
							<p>Labwork</p>
						</div>
					</div>
					<div className='gradebook-body'>
						{this.state.tab === "Exams" ? <MyExamGrade student = {this.props.student}/> : null}
						{this.state.tab === "Assignments" ? <MyAssignmentGrade student = {this.props.student}/> : null}
						{this.state.tab === "Homework" ? <MyHomeworkGrade />:null}
						{this.state.tab === "Labwork" ? <MyLabworkGrade />:null}
					</div>
				</div>
			</div>
		);
	}
}

export default MyGradeBook;
